import { Injectable } from '@angular/core';
import { IViewDesignerComponentModel } from '@helix/platform/view/api';

@Injectable({
  providedIn: 'root'
})
export class RxViewDesignerModels {
  private models: Map<string, IViewDesignerComponentModel> = new Map();

  clear(): void {
    this.models.clear();
  }

  set(guid: string, model: IViewDesignerComponentModel): void {
    this.models.set(guid, model);
  }

  get(guid: string): IViewDesignerComponentModel {
    return this.models.get(guid);
  }

  delete(guid: string): void {
    this.models.delete(guid);
  }
}
